import React,{useState}  from 'react';

import classnames from 'classnames';

import './style.css';
import '../../style.css';

const RadioButton = ({selected, disabled, onChange}) => {
  const [check, setCheck] = useState(false);

  const handleCheck = (e) => {
    if (!disabled) {
      onChange(e);
    }
  };

  return(
    <div onClick={handleCheck}>
      <div  className={classnames('radio flexible jCenter aCenter', {'radioDisabled': disabled, 'radioCheck':selected})}  >
        <div className={classnames('radioContent', {'radioContentDisabled': disabled, 'checkRadioContent': selected})}/>
      </div>
    </div>
  );
};

export default RadioButton;
