import React from 'react';
import { Link } from 'react-router-dom';
import Slider from 'react-slick';

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import './style.css';

import Image from '../../../components/Image';

const Carousel = (props) => (
    <div className='AppWrapper'>
        <Slider
            settings={{
                infinite: true,
                speed: 2000,
                slidesToShow: 1,
                slidesToScroll: 1,
                arrows: true,
                responsive: [
                    {
                        breakpoint: 320,
                        settings: {
                            arrows: false
                        }
                    }
                    ]
            }}
        >
          {props.sliders.map(item => {
            if (item.isSlide) {

              if(item.link) {
                return (
                  <Link to={item.link}>
                    <div>
                      <div key={item._id}  className='flexible vertical jCenter itemParent' >
                        <Image className='carousel-item' src={`/${item.image}`} />
                        <div className='item-text flexible jCenter'>
                          <span >{item.text}</span>
                        </div>
                      </div>
                    </div>
                  </Link>
                );
              } else {
                return (
                  <div>
                    <div key={item._id}  className='flexible vertical jCenter itemParent' >
                      <Image className='carousel-item' src={`/${item.image}`} />
                      <div className='item-text flexible jCenter'>
                        <span >{item.text}</span>
                      </div>
                    </div>
                  </div>
                );
              }
            }

            return null;
          })}
        </Slider>
    </div>
);

export default Carousel;


