const getTexts = async ({ state }) => {
  const response = await fetch('/api/public/texts', {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
    },
  });

  if (!response.ok) {
    return state.texts;
  }

  const texts = await response.json();
  return state.texts = texts;
};

