import React, { useState } from 'react';
import classNames from 'classnames';
import dayjs from 'dayjs';

import './style.css';

const months = ["January","February","March","April","May","June","July",
    "August","September","October","November","December"];

const days = [...Array(31).keys()];
const years = [...Array(101).keys()];

const currentYear = Number(dayjs().year());

const emailRegex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

const BookSecond = ({ texts, onChange, onSubmit, worker, total, time, services }) => {
    const [data, setData] = useState({
        name: '',
        lastName: '',
        phone: '',
        email: '',
        confirmEmail: '',
        post: '',
        city: '',
        address: '',
        homeNumber: '',
        additionalAddress: '',
        note: '',
        month: '',
        day: '',
        year: '',
    });

    const [errors, setErrors] = useState({
        name: false,
        lastName: false,
        phone: false,
        email: false,
        confirmEmail: false,
        post: false,
        city: false,
        address: false,
        homeNumber: false,
        additionalAddress: false,
        note: false,
        month: false,
        day: false,
        year: false,
    });

    const findText = (key) => {
        return texts.find(txt => txt.key === key).text;
    };

    const handleChange = (key, value) => {
        setData({ ...data, [key]: value });
    };

    const handleSubmit = e => {
        e.stopPropagation();
        e.preventDefault();
        const validation = { ...errors };
        let check = true;
        for(const key in data) {
            if (key !== 'note' && key !== 'additionalAddress' && !data[key]) {
                validation[key] = true;
                check = false;
            }

            if (key === 'email' && !emailRegex.test(data[key].toLowerCase())) {
                validation.email = true;
                check = false;
            }

            if(data.email && key === 'confirmEmail' && data[key] !== data.email) {
                validation.confirmEmail = true;
                check = false;
            }
        }

        if(check) {
            const { confirmEmail, day, year, month, ...dataForSending } = data;
            const birthday = dayjs(`${month}-${day}-${year}`).toDate();
            onSubmit({ ...dataForSending, birthday });
        } else {
            setErrors(validation);
        }
    };

    const desc = findText('BOOKING_SECOND_STEP_DESCRIPTION').split('{separator}');

    return(
        <div className = 'flexible vertical' style={{paddingTop: '30px'}}>
            <div className = 'flexible vertical'>
                <p>
                    {desc[0]}
                    {desc[0] &&  <><b>{services.join(', ')}</b> <b>{worker ? ' by ' : ''} {worker}</b></>}

                    {desc[1]}
                    {desc[1] &&  <b>{time.format('HH:mm')} auf {time.format('MMMM MM, YYYY')}</b>}

                    {desc[2]}
                    {desc[2] &&  <b>{total}</b>}

                    {desc[3]}
                </p>
                <div className='flexible vertical'>
                    <form>
                        <div className='flexible jBetween inputParent'>
                            <div className='flexible grow vertical'>
                                <p className='inputText '>{findText('BOOKING_FIRST_NAME_TEXT')}</p>
                                <input
                                  name='name'
                                  type="text"
                                  className={classNames('nameSname inputStyle mRight', { error: errors.name })}
                                  value={data.name}
                                  onChange={e => handleChange('name', e.target.value)}
                                />
                            </div>
                            <div className='flexible grow vertical'>
                                <p className='inputText' >{findText('BOOKING_LAST_NAME_TEXT')}</p>
                                <input
                                  name='lastName'
                                  type="text"
                                  className={classNames('nameSname inputStyle', { error: errors.lastName })}
                                  value={data.lastName}
                                  onChange={e => handleChange('lastName', e.target.value)}
                                />
                            </div>
                        </div>
                        <div className='inputParent flexible'>
                            <div className='flexible grow vertical mRight'>
                                <p className='inputText'>{findText('BOOKING_PHONE_TEXT')}</p>
                                <input
                                  name='phone'
                                  className={classNames('logContacts inputStyle', { error: errors.phone })}
                                  value={data.phone}
                                  onChange={e => handleChange('phone', e.target.value)}
                                />
                            </div>
                            <div  className='flexible grow vertical mRight'>
                                <p className='inputText'>{findText('BOOKING_EMAIL_TEXT')}</p>
                                <input
                                  name='email'
                                  className={classNames('logContacts inputStyle', { error: errors.email })}
                                  value={data.email}
                                  onChange={e => handleChange('email', e.target.value)}
                                />
                            </div>
                            <div  className='flexible grow vertical'>
                                <p className='inputText'>{findText('BOOKING_EMAIL_CONFIRM_TEXT')}</p>
                                <input
                                  name='confirmEmail'
                                  className={classNames('logContacts inputStyle', { error: errors.confirmEmail })}
                                  value={data.confirmEmail}
                                  onChange={e => handleChange('confirmEmail', e.target.value)}
                                />
                            </div>
                        </div>
                        <p className='booking-service-title'>{findText('BOOKING_ADDRESS_TEXT')}</p>
                        <div className='inputParent flexible'>
                            <div className='flexible grow vertical'>
                                <p className='inputText'>{findText('BOOKING_POST_TEXT')}</p>
                                <input
                                  name='post'
                                  type="text"
                                  className={classNames('geo inputStyle', { error: errors.post })}
                                  value={data.post}
                                  onChange={e => handleChange('post', e.target.value)}
                                />
                            </div>
                        </div>
                        <div className='inputParent flexible'>
                            <div className='flexible grow vertical'>
                                <p className='inputText'>{findText('BOOKING_CITY_TEXT')}</p>
                                <input
                                  name='city'
                                  type="text"
                                  className={classNames('geo inputStyle', { error: errors.city })}
                                  value={data.city}
                                  onChange={e => handleChange('city', e.target.value)}
                                />
                            </div>
                        </div>
                        <div className='inputParent flexible'>
                            <div className='flexible grow vertical'>
                                <p className='inputText'>{findText('BOOKING_ADDRESS_TEXT')}</p>
                                <input
                                  name='address'
                                  type="text"
                                  className={classNames('geo inputStyle', { error: errors.address })}
                                  value={data.address}
                                  onChange={e => handleChange('address', e.target.value)}
                                />
                            </div>
                        </div>
                        <div className='inputParent flexible'>
                            <div className='flexible grow vertical'>
                                <p className='inputText'>{findText('BOOKING_HOME_NUMBER_TEXT')}</p>
                                <input
                                  name='homeNumber'
                                  type="text"
                                  className={classNames('geo inputStyle', { error: errors.homeNumber })}
                                  value={data.homeNumber}
                                  onChange={e => handleChange('homeNumber', e.target.value)}
                                />
                            </div>
                        </div>
                        <div className='inputParent flexible'>
                            <div className='flexible grow vertical'>
                                <p className='inputText'>{findText('BOOKING_ADDITIONAL_ADDRESS_TEXT')}</p>
                                <input
                                  name='additionalAddress'
                                  type="text"
                                  className={classNames('geo inputStyle', { error: errors.additionalAddress })}
                                  value={data.additionalAddress}
                                  onChange={e => handleChange('additionalAddress', e.target.value)}
                                />
                            </div>
                        </div>
                        <div className='inputParent flexible aEnd'>
                            <div className='flexible grow vertical mRight'>
                                <p className='inputText'>{findText('BOOKING_BIRTHDAY_TEXT')}</p>
                                <select
                                  className={classNames('selectStyle', { error: errors.month })}
                                  style={{marginRight: '15px'}}
                                  value={data.month}
                                  onChange={e => handleChange('month', e.target.value)}
                                >
                                    <option value=''>{findText('BOOKING_CHOOSE_MONTH_TEXT')}</option>
                                    {months.map(m => <option value={m}>{m}</option>)}
                                </select>
                            </div>
                            <div className='flexible grow vertical mRight'>
                                <select
                                  className={classNames('selectStyle', { error: errors.day })}
                                  style={{marginRight: '15px'}}
                                  value={data.day}
                                  onChange={e => handleChange('day', e.target.value)}
                                >
                                    <option value=''>{findText('BOOKING_SELECT_DAY_TEXT')}</option>
                                    {days.map(d => <option value={d+1}>{d+1}</option>)}
                                </select>
                            </div>
                            <div className='flexible grow vertical'>
                                <select
                                  className={classNames('selectStyle', { error: errors.year })}
                                  value={data.year}
                                  onChange={e => handleChange('year', e.target.value)}
                                >
                                    <option value=''> {findText('BOOKING_SELECT_YEAR_TEXT')}</option>
                                    {years.map(y => <option value={currentYear-y}>{currentYear-y}</option>)}
                                </select>
                            </div>
                        </div>
                        <div className='inputParent'>
                            <p className='inputText'>{findText('BOOKING_NOTE_TEXT')}</p>
                            <textarea
                              name='note'
                              className={classNames('bookTextArea', { error: errors.note })}
                              value={data.note}
                              onChange={e => handleChange('note', e.target.value)}
                            />
                        </div>
                        <div className='buttonsParent flexible jBetween'>
                            <button className='bookButton' onClick={()=> onChange('dec')}>{findText('BOOKING_BACK_BUTTON_TEXT')}</button>
                            <button className='bookButton' onClick={handleSubmit}>{findText('BOOKING_NEXT_BUTTON_TEXT')}</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>

    )
};

export default BookSecond
