import React from 'react';
import classnmaes from 'classnames';
import './style.css';

const LineText = ({
  width,
  headTitle,
  textTitleLeft,
  textTitleRight,
  lineStyle,
  textColor,
  heading,
  style,
  flexName,
  className
}) => {
  const isHeading = heading === false;

  return (
    <div
      style={{
      width: width,
      borderBottom: !isHeading && lineStyle,
        ...style,
    }}
      className="LineText">
      <div className={classnmaes('textInner', className, {isHeading})}>
        {headTitle ? <h3 style={{color: textColor}} className='head-title'>
              {headTitle}
            </h3>
            :
            <div className={classnmaes('textTitle', flexName)}>
              <div className="textTitleLeft">
                <p style={{lineHeight:'35px', padding: '20px 0 0'}}>{textTitleLeft}</p>
              </div>
              <p style={{ paddingBottom: 0 }}>{textTitleRight}</p>
            </div>
        }
      </div>
    </div>
  )
}

export default LineText;
