import React, {useState} from 'react';
import { NavLink } from 'react-router-dom';
import classnames from 'classnames';
import BurgerMenu from '../BurgerMenu';
const Dropdown = ({
    className,
    onClick,
    opened,
  texts = [],
    whenResize,
  options,
}) => {
    const [state, setState] = useState('');
    const handleState = e => {
        onClick()
    };

    const findText = (key) => {
        return  texts.find(txt => txt.key === key) ? texts.find(txt => txt.key === key).text : '';
    };

    const handle =() => {
        setState('close');
    };

    const leaveHandler = e => {
        setState('');
    };

    return (
        <>
            {
                whenResize ?
                    <ul  className={classnames({'dropdownMenu' : opened, 'whenResize' : className})}>
                        <li onClick={handleState}>
                            <NavLink exact to='/'>
                                {findText('HOME_MENU_TEXT')}
                            </NavLink>
                        </li>
                        <li onClick={handleState}>
                            <a href="#leistungen">
                                {findText('SERVICE_MENU_TEXT')}
                            </a>
                        </li>
                      {options.map(page => (
                        <li className='child' onClick={handleState}>
                          <NavLink to={`/services/${page.alias}`}>
                            {page.title}
                          </NavLink>
                        </li>
                      ))}
                        <li onClick={handleState}>
                            <NavLink to='/contact'>
                                {findText('CONTACT_MENU_TEXT')}
                            </NavLink>
                        </li>
                        <li onClick={handleState}>
                            <NavLink to='/booking'>
                                {findText('BOOKING_MENU_TEXT')}
                            </NavLink>
                        </li>
                        <li onClick={handleState}>
                            <NavLink to='/confidentiality'>
                                {findText('DATA_PROTECTION_MENU_TEXT')}
                            </NavLink>
                        </li>
                        <BurgerMenu/>
                    </ul>
                    :
                    <ul onMouseLeave={leaveHandler}  className={classnames('dropdownMenu',state)}>
                      {options.map(page => <li onClick={handle}>
                        <NavLink to={`/services/${page.alias}`}>
                          {page.title}
                        </NavLink>
                      </li>)}
                        <BurgerMenu/>
                    </ul>
            }
        </>
    )
}
export default Dropdown;
