import React, { useState, useEffect } from 'react';
import { FaChevronDown } from "react-icons/fa";
import { NavLink } from 'react-router-dom';
import { Link } from 'react-router-dom';
import classnames from 'classnames';

import { useStore } from '../../../store/DataProvider';

import { BurgerMenu, Dropdown } from '../../index';
import { Image } from '../../index';

import logo from '../../../assets/logo.png';
import './style.css';

const Header = () => {
    const { state: { texts, servicePages }} = useStore();

    const [whenClickOnBurger, setwhenClickOnBurger] = useState(false);
    const [showOrNot, setshowOrNot] = useState(false);

    const findText = (key) => {
        return texts.find(txt => txt.key === key).text;
    };

    const handleClick = () => {
        setwhenClickOnBurger(!whenClickOnBurger);
    };

    const handleShowOrNot = () => {
        window.scrollY > 10 ? setshowOrNot(true) : setshowOrNot(false);
    };

    useEffect(() => {
        window.addEventListener('scroll', handleShowOrNot)
    });

    return (
        <div className={classnames('flexible jBetween Header pageContent aCenter', {'showOrNot' : showOrNot})}>
            <Link to='/'>
                <div>
                    <Image src={logo} className='headerLogo'></Image>
                </div>
            </Link>
            <div className="container purple topBotomBordersIn flexible aCenter">
                <ul className='flexible'>
                    <div className="contentSecondLi">
                        <li>
                            <NavLink exact to='/'>
                                {findText('HOME_MENU_TEXT')}
                            </NavLink>
                        </li>
                    </div>
                    <div className="contentSecondLi">
                        <li>
                            <a href='#leistungen'>
                                {findText('SERVICE_MENU_TEXT')}
                                <FaChevronDown className="FaChevronDown" />
                            </a>
                            <Dropdown
                              texts={texts}
                              options={servicePages}
                                onClick = {handleClick}
                                opened = {whenClickOnBurger}
                                show
                            />
                        </li>
                    </div>
                    <div className="contentSecondLi">
                        <li>
                            <NavLink to='/contact'>
                                {findText('CONTACT_MENU_TEXT')}
                            </NavLink>
                        </li>
                    </div>
                    <div className="contentSecondLi">
                        <li>
                            <NavLink to='/booking'>
                                {findText('BOOKING_MENU_TEXT')}
                            </NavLink>
                        </li>
                    </div>
                    <div className="contentSecondLi">
                        <li>
                            <NavLink to='/confidentiality'>
                                {findText('DATA_PROTECTION_MENU_TEXT')}
                            </NavLink>
                        </li>
                    </div>
                </ul>
                <BurgerMenu
                    onClick = {handleClick}
                />
                <Dropdown
                  texts={texts}
                    onClick = {handleClick}
                    opened
                    whenResize
                    options={servicePages}
                    className={whenClickOnBurger}
                />
            </div>
        </div>
    )
}
export default Header
