import React from 'react';
import Spinner from 'react-spinner-material';

const BookLast = ({ loading }) => {
  if (loading) {
    return (
      <div style={{
        flex: 1,
        display: 'flex',
        height: '100%',
        justifyContent: 'center',
        flexDirection: 'column',
        paddingTop: '30px',
        width: '100%',
        textAlign: 'center',
        alignItems: 'center',
        backgroundColor: '#666'
      }}>
        <Spinner size={120} spinnerColor="#a6815b" spinnerWidth={2} visible={true} />
      </div>
    );
  }
    return(
        <div style={{paddingTop: '30px'}}>
            <p>
                Vielen Dank! Ihre Buchung ist abgeschlossen. Sie haben eine E-Mail mit Details zu Ihrer Buchung erhalten.
            </p>
        </div>
    )
};

export default BookLast;
