import React, { useMemo, useState } from 'react';
import dayjs from 'dayjs';
import isBetween from 'dayjs/plugin/isBetween';
import { IoMdCheckmark } from 'react-icons/io'

import { BookItem, BookTimeBlock } from "../../components";

import './style.css';
import classnames from 'classnames';

dayjs.extend(isBetween);

function checkDisabledSchedule(value, schedules) {
    let disabled = false;
    schedules.forEach(({ startDate, endDate }) => {
        const start = dayjs(startDate);
        const end = dayjs(endDate);

        if (value.isBetween(start, end)) {
            disabled = true;
        }
    });

    return disabled;
}

const BookFirst = ({
    onChange,
    services,
    toggleService,
    total,
    subTotal,
    schedules,
    workers,
    serviceIds,
    changeTime,
    changeWorker,
    texts,
}) => {
    const [worker, setWorker] = useState(null);
    const [time, setTime] = useState(null);

    const findText = (key) => {
        return texts.find(txt => txt.key === key).text;
    };

    const handleTimeChange = time => {
        setTime(time);
        changeTime(time);
    };

    const handleWorkerChange = id => {
        setWorker(id);
        changeWorker(id);
    };

    const renderTimeBlocks = date => {
        const result = [];
        let i = true;
        let period = 30;
        const fixedDate = date.hour(9).minute(30).second(0);
        do {
            const dateWithChangedRef = dayjs(fixedDate.add(period, 'm').format());
            result.push(<BookTimeBlock
              onChange={() => handleTimeChange(dateWithChangedRef)}
              selected={time && time.format() === fixedDate.add(period, 'm').format()}
              disabled={checkDisabledSchedule(fixedDate.add(period, 'm'), schedules)}
              time={fixedDate.add(period, 'm').format('HH:mm')}
            />);
            if (fixedDate.add(period, 'm').hour() === 19) {
                i = false;
            }
            if (fixedDate.day() === 6 && fixedDate.add(period, 'm').hour() === 14) {
                i = false;
            }
            period += 30;
        } while (i);

        return result;
    };

    const dates = useMemo(() => {
        const columns = [];
        let day = dayjs();
        for(let i = 0; i < 8; i++) {
            day.day() === 0 ? i-- : columns.push(day);
            day = day.add(1, 'd');
        }
        return columns;
    }, []);

    return (
        <div className='booking-select-service'>
            <div style={{paddingTop: '30px'}}>
                <span className='booking-service-title'>{findText('BOOKING_SELECT_SERVICE_TEXT')}:</span>
            </div>
            {services.map(cat => (
              cat.services.length > 0 && <div className='category'>
                  <div>
                      <p className='active-color'>{cat._doc.title}</p>
                      {cat.services.map(service => (
                        <BookItem
                          onClick={() => toggleService(service._id)}
                          service={service.title}
                          price={`${service.price} `}
                        />
                      ))}
                  </div>
              </div>
            ))}
            <div className='category'>
                <div>
                    <p className='active-color'>
                        {findText('BOOKING_TOTAL_TEXT')}:
                        <span style={{ float: 'right' }}>
                            <span style={{ textDecoration: 'line-through', fontSize: 16 }}>{subTotal}  </span>
                            {total} 
                        </span>
                    </p>
                </div>
            </div>
            <div className='category'>
                <div>
                    <p className='active-color'>{findText('BOOKING_EMPLOYEE_TEXT')}</p>
                    <div className={classnames( 'BookItem flexible aCenter jBetween',  {'actBook': worker === null }  )} onClick={() => setWorker(null)}>
                        <div className='flexible aCenter'>
                            <div className={classnames( 'itemIcon flexible aCenter jCenter',  {'activeItemIcon': worker === null })} >
                                { worker === null ? <IoMdCheckmark fill='ghostWhite'/> : null}
                            </div>
                            <span  className={classnames( 'itemFont flexible aCenter',  {'activeItemFont': worker === null })}>{findText('BOOKING_ANY_EMPLOYEE_TEXT')}</span>
                        </div>
                    </div>
                    {workers.map(item => {
                        const active = item._id === worker;
                        const support = serviceIds.every(element => item.serviceIds.indexOf(element) > -1);

                        if (!support) {
                            return null;
                        }

                        return (
                          <div className={classnames( 'BookItem flexible aCenter jBetween',  {'actBook':active }  )} onClick={() => handleWorkerChange(item._id)}>
                              <div className='flexible aCenter'>
                                  <div className={classnames( 'itemIcon flexible aCenter jCenter',  {'activeItemIcon': active })} >
                                      { active ? <IoMdCheckmark fill='ghostWhite'/> : null}
                                  </div>
                                  <span  className={classnames( 'itemFont flexible aCenter',  {'activeItemFont': active })}>{item.name}</span>
                              </div>
                          </div>
                        );
                    })}
                </div>
            </div>
            <div className='book-second-step'>
                <div className='row flexible jBetween'>
                    {dates.map(date => {
                        return (
                          <div className='column flexible vertical'>
                              <div className='dateBlock active-block'>
                                  <span className='dateText' >{date.format('ddd, MMM DD')}</span>
                              </div>
                              {renderTimeBlocks(date)}
                          </div>
                        );
                    })}
                </div>
                <div style={{borderTop: '1px solid #666'}} className='flexible jEnd'>
                    <button
                      className={classnames('bookButton', { disabled: !serviceIds.length || !time })}
                      onClick={onChange}>
                        {findText('BOOKING_NEXT_BUTTON_TEXT')}
                    </button>
                </div>
            </div>
        </div>
    )
};

export  default BookFirst;
