import React, { useEffect, useState } from 'react';
import { Route, Switch } from 'react-router';

import Spinner from 'react-spinner-material';

import { useStore } from '../store/DataProvider';

import Main from './Main';
import ServicePage from './ServicePages';
import Confidentiality from "./Confidentiality";
import Contact from './Contact';
import DateBooking from "./DateBooking";


import { Footer, Header } from '../components';

export default function Container() {
    const { state: { servicePages }, actions: { getSliders, getTexts, getServices, getWorkers, getSchedules, getServicePages } } = useStore();

    const [loading, setLoading] = useState(true);

    useEffect(() => {
        setLoading(true);
        Promise.all([getSliders(), getTexts(), getServices(), getWorkers(), getSchedules(), getServicePages()]).then(() => setLoading(false));
    }, [getSliders, getTexts, getServices, getWorkers, getServicePages]);

    if (loading) {
        return (
          <div style={{
              flex: 1,
              display: 'flex',
              height: '100%',
              justifyContent: 'center',
              flexDirection: 'column',
              width: '100%',
              textAlign: 'center',
              alignItems: 'center',
              backgroundColor: '#666'
          }}>
              <Spinner size={120} spinnerColor="#a6815b" spinnerWidth={2} visible={true} />
          </div>
        );
    }

  return (
      <>
        <Header />
        <Switch>
          <Route exact path="/" component={Main} />
          <Route exact path="/booking" component={DateBooking} />
          <Route exact path="/contact" component={Contact} />
          <Route exact path="/confidentiality" component={Confidentiality} />
          {servicePages.map(page =>
            <Route
              exact
              path={`/services/${page.alias}`}
              component={props => <ServicePage page={page} {...props} />}
            />
          )}
        </Switch>
        <Footer/>
      </>
    );
}
