import React, { Component } from 'react';
import { compose, withProps } from "recompose";
import { withScriptjs, withGoogleMap, GoogleMap, Marker } from 'react-google-maps';
// import mapStyled from './mapStyled';

const MyMapComponent = compose(
    withProps({
        googleMapURL:   "https://maps.googleapis.com/maps/api/js?key=AIzaSyA2VEv31f9riUUSVUz1S8tWITJl-0yp_9U&v=3.exp&libraries=geometry,drawing,places",
        loadingElement:   <div style={{ height: `100%` }} />,
        containerElement:    <div style={{ height: `100%` }} />,
        mapElement:    <div style={{ height: `100%` }} />
    }),
    withScriptjs,
    withGoogleMap
)(props => (
    <GoogleMap  defaultZoom={18} defaultCenter={{lat: 48.13768415070393, lng: 11.52958550490439 }}>
        {props.isMarkerShown && (
            <Marker position={{ lat: 48.13768415070393, lng: 11.52958550490439 }} />
        )}
    </GoogleMap>
));

class ContactMap extends Component {
    render() {
        return (
            <MyMapComponent isMarkerShown className='google-map' />
        )
    }
}

export default ContactMap;
